.header
{
  width: 100%;
  height: 2.1em;
  
  font-size: 3em;
  background-color: #303030;
}

.header > div
{
  margin-left: auto;
  margin-right: auto;
  margin-top: 0.3em;
}

.header > div > span
{
  font-weight: bold;
  font-family: "Comic Sans MS", cursive, sans-serif;
  color: #2BB673;

  margin-top: auto;
  margin-bottom: auto;
  margin-left: 0.5em;
}

.header > div > img
{
  width: 0.92em;
  height: 1.59em;
}
.footer
{
  width: 100%;
  min-height: 2.5em;
  font-size: 1.5em;

  margin-top: auto;
  justify-content: center;
  align-items: flex-end;

  color: #F0F0F0;
  background-color: #303030;
}

.footer > label
{
  font-weight: bold;
  margin-bottom: 1em;
}
.navBar
{
  width: 100%;
  justify-content: center;
  background-color: transparent;

  height: 3.5em;

  margin-bottom: 4em;
}

.navBar > div
{
  width: 25%;
  background-color: #303030;
  justify-content: center;
}

.navBar > div > label
{
  background-color: #303030;
  cursor: pointer;
  color: white;
  font-size: 1.6em;

  border-style: solid;
  border-color: #303030;

  align-items: center;

  padding-left: 0.5em;
  padding-right: 0.5em;
}

.navBar > div > label:hover
{
  background-color: #2BB673;
  border-color: #2BB673;
}

.navOn
{
  background-color: #2BB673 !important;
  border-color: #2BB673 !important;
  font-weight: bold;
}

.navBar > span
{
  min-width: 25%;
  max-width: 25%;
}

.navBar > span:first-child
{
  background-image: -webkit-linear-gradient(
      45deg, transparent 50%, #303030 50%);
}

.navBar > span:last-child
{
  background-image: -webkit-linear-gradient(
      135deg, transparent 50%, #303030 50%);
}
.formArea
{
  width: 30%;
}

.formArea > div
{
  margin-top: 0.88em;
  margin-bottom: 0.88em;

  align-items: center;

  padding-top: 0.63em;
  padding-bottom: 0.63em;
  padding-left: 0.63em;
  padding-right: 0.63em;

  background-color: #606060;
  box-shadow: 0 0 0.1em 0.04em grey;
}

.formArea > div > form
{
  min-width: 60%;
  max-width: 60%;
}

.formArea > div > form > div:last-child > button:first-child
{
  margin-left: auto;
}

.formArea > div > form > div:last-child > button:not(:last-child)
{
  margin-right: 0.63em;
}

.formArea > div > form .itTalentFormMessage
{
  text-align: center;
}
.personForm
{
  margin-top: 1.25em;
}

.personForm > div:not(last-child)
{
  margin-bottom: 1em;
}

.personForm > div > label
{
  min-width: 5em;
  margin-top: auto;
  padding: 0;
}

.personForm > div > input
{
  flex: 1 1;
  -webkit-appearance: textfield;
     -moz-appearance: textfield;
          appearance: textfield;
  color: white;

  padding-left: 0.5em;
  padding-right: 0.5em;
}

.skillForm
{
  margin-top: 1.25em;
}

.skillForm > div > input
{
  flex: 1 1;
  color: white;

  padding-left: 0.5em;
  padding-right: 0.5em;
}

.skillForm > div:not(last-child)
{
  margin-bottom: 0.5em;
}

.skillForm > div:not(:last-child) > label
{
  margin-top: auto;
}

.skillForm > div > label
{
  min-width: 3.25em;
  padding: 0;
}

.skillForm > div:last-child > label
{
  margin-bottom: 0.25em;
}

.skillForm > div > textarea
{
  background-color: transparent;
  color: white;
  font-size: 1em;

  border-width: 0.07em;
  border-color: #2BB673;
  border-style: solid;

  padding: 0.5em;

  height: 20em;
  resize: none;
}

.skillForm > div > textarea:focus
{
  outline: none;
  box-shadow: none;
}

.skillForm > div > textarea:disabled
{
  background-color: #303030;
}
.itemEditArea > div
{
  align-items: center;

  padding-top: 0.63em;
  padding-bottom: 0.63em;
  padding-left: 0.63em;
  padding-right: 0.63em;

  background-color: #606060;
  box-shadow: 0 0 0.1em 0.04em grey;
}

.itemEditArea > div:last-child
{
  align-items: flex-end;
}
.listArea > div
{
  align-items: center;

  min-width: 100%;
  max-width: 100%;
}

.listArea > div > div:last-child
{
  width: 100%;
  justify-content: flex-end;

  margin-top: 1.5em;
}

.listArea > div > span
{
  width: 100%;
  text-align: center;

  padding-top: 1.5em;
  padding-bottom: 1.5em;
  padding-left: 0;
  padding-right: 0;
}
.listView
{
  min-width: 100%;
  max-width: 100%;
}

.listView > label
{
  text-align: center;

  padding: 1.5em;
}
.personListItem > div
{
  justify-content: space-between;
  margin-top: 1.5em;
}

.personListItem > div > div:first-child
{
  justify-content: space-between;
}

.personListItem > div > div:nth-child(2)
{
  margin-top: 0.88em;
  margin-bottom: 1.63em;
}

.personListItem > div >
    div:nth-child(2) > label:first-child
{
  color: #2BB673;
  font-weight: bold;
}

.personListItem > div > div:last-child
{
  margin-left: auto;
}

.personListItem > div > div:last-child > button
{
  margin-left: 0.63em;
}
.skillListItem > div
{
  margin-top: 1.5em;
  min-width: 100%;
  max-width: 100%;
}

.skillListItem > div > div:last-child
{
  margin-left: auto;
  margin-top: 1.63em;
}

.skillListItem > div > div:last-child > button
{
  margin-left: 0.63em; 
}
.itemEdit > div:first-child
{
  margin-bottom: 2em;
}

.itemEdit > div > label
{
  color: white;
  font-weight: bold;

  margin-bottom: 0.25em;
}

.itemEdit > div > div
{
  flex-wrap: wrap;

  border: solid;
  border-color: #2BB673;
  border-width: 0.07em;

  padding: 0.5em;
}

.itemEdit > div > div > button
{
  cursor: pointer;
  margin: 0.5em;
}

.itemEdit > div > div > button > label
{
  cursor: pointer;
  background: #2BB673;
  
  width: 1em;
  height: 1em;

  margin-top: auto;
  margin-bottom: auto;
  margin-left: 0.5em;
  padding: 0;

  font-size: 1em;
  line-height: 1em;
  text-align: center;

  border: solid;
  border-color: #2BB673;
  border-width: 0.07em;
}

.personSkillItemEdit
{
  width: 100%;
}

.personSkillItemEdit > div:nth-child(2)
{
  justify-content: space-between;

  margin-top: 1.5em;
  margin-bottom: 2em;
}

.personSkillItemEdit > h3
{
  color: #2BB673;
  width: 100%;

  margin: 0px;

  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
}
.searchbar
{
  width: 100%;
  background-color: #303030;
  color: white;

  padding-top: 1em;
  padding-bottom: 1.25em;
}

.searchbar > form
{
  margin-left: auto;
  margin-right: auto;
}

.searchbar > form > label
{
  margin-top: auto;
  margin-right: 0.63em;
}

.searchbar > form > input
{
  min-width: 15em;
  color: white;
}

.searchbar > form > div
{
  margin-left: 0.63em;
}

.searchbar > form > div > select:first-child
{
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;

  margin-right: 0.63em;
}
.mainArea
{
  background-color: #606060;
  color: #303030;
  font-size: 1em;

  min-height: 100%;

  align-items: center;
}

.mainArea > div:last-child
{
  background-color: transparent;
  justify-content: center;

  width: 100%;
  height: 3.5em;

  margin-top: auto;
}

.mainArea > div:last-child > span
{
  min-width: 25%;
  max-width: 25%;
  min-height: 100%;
  max-height: 100%;
}

.mainArea > div:last-child > span:nth-child(2)
{
  background-color: #303030;
}

.mainArea > div:last-child > span:first-child
{
  background-image: -webkit-linear-gradient(
      135deg, #303030 50%, transparent 50%);
}

.mainArea > div:last-child > span:last-child
{
  background-image: -webkit-linear-gradient(
      45deg, #303030 50%, transparent 50%);
}
html
{
  height: 100%;
  width: 100%;
}

body
{
  height: 100%;
  width: 100%;
  color: white;

  min-width: 912px;
  min-height: 513px;

  display: flex;
  flex-direction: column;
  justify-content: space-between 
}

html, body
{
  margin: 0px;
  padding: 0px;
}

.app
{
  width: 100%;
  height: 100%;
}

.flexItemsCol
{
  display: flex;
  flex-direction: row;
}

.flexItemsRow
{
  display: flex;
  flex-direction: column;
}

.itTalentButton, .itTalentDarkButton,
.itTalentComboBox
{
  cursor: pointer;
  color: white;

  border-style: solid;
  font-size: 0.85em;

  padding-top: 0.19em;
  padding-bottom: 0.19em;
  padding-left: 0.38em;
  padding-right: 0.38em;
}

.itTalentButton, .itTalentComboBox
{
  background-color: #2BB673;
  border-color: #2BB673;
}

.itTalentDarkButton
{
  background-color: #303030;
  border-color: #303030;
}

.itTalentComboBox
{
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;

  -moz-text-align-last: center;

       text-align-last: center;
}

.itTalentButton:focus, .itTalentDarkButton:focus,
.itTalentComboBox:focus, .itTalentTextField:focus
{
  outline: none;
  box-shadow: none;
}

.itTalentTextField
{
  background-color: transparent;
  color: #303030;
  text-align: center;
  font-size: 0.85em;

  border-top: 0;
  border-right: 0;
  border-left: 0;
  border-width: 0.07em;
  border-bottom-color: #2BB673;
}

.itTalentTextField:disabled
{
  background-color: #303030;
}

.contentArea
{
  background-color: #303030;
  color: white;

  min-width: 55%;
  max-width: 55%;

  padding: 2em;
}

.sectionTitle
{
  background-color: #2BB673;
  color: white;

  font-weight: bold;
  font-size: 2em;
  text-align: center;

  width: 100%;

  padding-top: 0.38em;
  padding-bottom: 0.38em;
  
  margin-bottom: 2.25em;

  box-shadow: 0 0 0.1em 0.04em #6fdcaf;
}

.listItem
{
  background-color: #606060;

  min-width: 90%;
  
  margin-top: 0.88em;
  margin-bottom: 0.88em;

  padding-top: 0.38em;
  padding-bottom: 0.63em;
  padding-left: 0.63em;
  padding-right: 0.63em;

  box-shadow: 0 0 0.1em 0.04em grey;
}

.listItem > h3
{
  color: #2BB673;
  width: 100%;

  margin: 0px;

  text-align: center;
  font-size: 1.5em;
  font-weight: bold;
}

.itTalentFormMessage
{
  color: #2BB673;
  font-weight: bold;

  margin-top: 1.63em;
  margin-bottom: 1.63em;
}
